.Form-contrato {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  height: 330px;
  width: 40vw;
  padding-bottom: 50px;
}

.title-contrato {
  text-align: center;
  font: normal normal 600 20px/30px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

.input-container {
  display: flex;
  flex-direction: row;

  width: 300px;
  justify-content: space-between;
}

.inputs-contrato {
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  text-align: left;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  color: #121212;
  border-style: solid;
  border-color: #e9e9e9;
  border-width: 2px;
  border-radius: 10px;
  z-index: 999;
  height: 55px;
  width: 90px;
}
.modal-40w {
  background-color: transparent;
  width: 524px;
  height: 384px;
}

.labels-contrato {
  text-align: left;
  font: normal normal bold 12px/18px Poppins;
  letter-spacing: 0px;
  color: black;
}
