.home-title {
  text-align: left;
  font: normal normal 600 20px/30px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

.home-subtitle {
  text-align: left;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  color: #8f8f8f;
  opacity: 1;
}

.cardLeft {
  height: 275px;
  width: 460px;
  margin-right: 35px;
  box-shadow: 0px 3px 6px #cccccc80;
  border-radius: 10px;
  background-color: white;
}

.cardRight {
  height: 275px;
  width: 460px;
  margin-left: 35px;
  box-shadow: 0px 3px 6px #cccccc80;
  background-color: white;
  border-radius: 10px;
}

.leftTitle {
  padding-top: 20px;
  padding-left: 25px;
  text-align: left;
  font: normal normal 600 16px/25px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

.rightTitleCol {
  padding-top: 20px;
  padding-right: 35px;
}

.rightTitle {
  text-align: right;
  font: normal normal 300 12px/18px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

.Viewers {
  text-align: right;
  font: normal normal 600 16px/25px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

.main-container-home {
  display: flex;
  flex-direction: row;
  padding-left: 70px;
  padding-right: 60px;
}

.config-col {
  padding-left: 60px;
}

.home-button {
  height: 50px;
  width: 55px;
  border: none;
  background-color: transparent;
}

.home-input {
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  width: 344px;
  height: 54px;
  padding-left: 20px;
  padding-right: 20px;
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 2px solid #e9e9e9;
  opacity: 1;
  text-align: left;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  color: #121212;
}
.home-input::placeholder {
  color: #cccccc;
}

.home-form {
  display: flex;
  align-items: center;
}

.color-block {
  border: thin;
  border-width: 2px;
  border-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  border-radius: 10px;
  margin: 5px;
}

.pickerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.home-buttons-cont {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.home-page {
  padding-bottom: 100px;
}

.grid-charts {
  padding: 32px 15px 32px 60px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-count: 2;
  gap: 32px;
}

@media (max-width: 1024px) {
  .grid-charts {
    grid-template-columns: 1fr;
  }
}
