.app-container {
  height: 100vh;
  padding-left: 3%;
  padding-top: 0px;
  overflow: auto;
}
.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px;
  /* padding-top: 20px; */
  background-color: #fcfcfc;
}

.sidebar-row {
  flex: 1;
}
.container-searchbar {
  padding-left: 76px;
}
.input-group {
  max-width: 235px;
  border-radius: 10px;
}
.form-control,
.form-control:focus {
  border-radius: 10px;
  background-color: #f7f7f7;
  outline: none;
  box-shadow: none !important;
  border: 0px solid #ccc !important;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  text-align: left;
  font: normal normal normal 14px/21px Poppins;
  letter-spacing: 0px;
  color: #cacaca;
}
.search-button,
.search-button:hover,
.search-button:focus {
  background-color: #f7f7f7;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: none !important;
  border: 0px solid rgb(88, 13, 13) !important;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  padding-left: 5px;
  padding-top: 0px;
  padding-bottom: 0px;
}

.fa-search {
  color: #cacaca;
  padding: 10px;
}
/* .bell {
  display: flex;
  justify-content: flex-start;
  justify-content: flex-end;
  align-items: center;
  margin-right: 120px;
} */
.row-nav {
  padding-top: 30px;
  color: #121212;
}
.containerbutton {
  align-items: center;
}
.container-sidebar {
  padding: 0px;
}
.user-button {
  width: 144px;
  background-color: #feff46;
  color: #121212;
  font: normal normal 600 14px/21px Poppins;
  letter-spacing: 0px;
  border-radius: 10px;
}
.user-button:hover {
  background-color: #121212;
}
.logo {
  margin-bottom: 30px;
}
.sidebar-buttons {
  width: 100%;
  text-align: left;
  border-radius: 5px;
  height: 64px;
  background-color: #fcfcfc;
  font: normal normal 700 15px/25px Poppins;
  letter-spacing: 0px;
  color: #121212;
}

.manage-button,
.manage-button:hover,
.manage-button:active,
.manage-button:focus {
  width: 100%;
  text-align: left;
  border-radius: 5px;
  height: 64px;
  background-color: #1664c5;
  font: normal normal 600 15px/25px Poppins;
  letter-spacing: 0px;
  color: white;
  cursor: default;
}

.nova-transmissao {
  width: 100%;
  text-align: left;
  border-radius: 5px;
  height: 64px;
  background-color: #121212;
  font: normal normal 600 16px/25px Poppins;
  letter-spacing: 0px;
}
.fa-plus {
  margin-right: 12px;
}
.main-container {
  /* height: 70vh;
  padding-left: 60px;
  padding-top: 20px; */
  padding: 15px 15px 0 60px;
  min-height: 600px;
  overflow: auto;
  margin-top: 20px;
}
.title-row {
  padding-top: 70px;
}
.title-col {
  padding-left: 60px;
}
.title {
  text-align: left;
  font: normal normal 600 20px/30px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
  width: 100px;
}
.button-col {
  padding-right: 50px;
  display: flex;
  justify-content: flex-end;
}
.button-add-user {
  display: flex;
  align-items: center;
  background-color: #24446c;
  width: 227px;
  border-radius: 10px;
  justify-content: flex-end;
  padding-right: 25px;
  font: normal normal 600 16px/25px Poppins;
  letter-spacing: 0px;
  color: #ffffff;
}
.fa-user-plus {
  margin-left: 10px;
}

.fa-folder {
  color: black;
  margin-right: 12px;
}

.fa-video {
  color: black;
  margin-right: 12px;
}

.fa-tv {
  color: white;
  margin-right: 12px;
}

.fa-home {
  color: black;
  margin-right: 12px;
}

.home-icon {
  color: green;
  margin-right: 12px;
}
