.carouselcont {
  position: relative;
  display: flex;
  grid-area: carousel;
  height: 684px;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.carousel {
  width: 455px;
  height: 299px;
  text-align: center;
  background: transparent;
  color: transparent;
  font-family: sans-serif;
  font-size: 12px;
  text-transform: uppercase;
  border-radius: 10px;
  box-sizing: border-box;
}

.img {
  width: 455px;
  height: 100%;
  border-radius: 10px;
}
