.col-addCam {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.grouping-test {
  background-color: transparent;
}
.Form-addCamera {
  padding-left: 30px;
}
@media (max-width: 768px) {
  .Form-addCamera {
    padding: 0;
  }
  .Form-addCamera .grouping {
    /* width: 10rem !important; */
    width: 100%;
  }
  .Form-addCamera input {
    /* width: 10rem !important; */
    width: 100%;
  }
}
.row-addCam {
}
.title-addCam {
  text-align: center;
  font: normal normal 600 20px/30px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}
.col-addCam-title {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin-top: 50px;
}

.col-addCam-input {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin-top: 20px;
}

.col-addCam-button {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin-top: 45px;
  padding-bottom: 70px;
}
