.transmit-title-col {
  padding-left: 60px;
}

.transmit-button-col {
  padding-right: 50px;
  display: flex;
  justify-content: flex-start;
}

.button-changeCam {
  display: flex;
  align-items: center;
  background-color: #24446c;
  width: 240px;
  border-radius: 10px;
  justify-content: center;
  padding-right: 25px;
  font: normal normal 600 16px/25px Poppins;
  letter-spacing: 0px;
  color: #ffffff;
}

.button-killTransmission {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #378a13 0% 0% no-repeat padding-box;
  width: 240px;
  border-radius: 10px;
  margin-left: 40px;
  opacity: 1;
  font: normal normal 600 16px/25px Poppins;
  letter-spacing: 0px;
  color: #ffffff;
}


.container-legendas {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 8px;
  border: solid 8px #e9e9e9;
  width: 33%;
  background-color: #e9e9e9;
  margin-left: 20px;
  border-radius: 5px; /* conforme prototipo*/
  overflow-y: scroll;
  height: auto;
}
/* atualizao do estilo do scroll */
.container-legendas::-webkit-scrollbar {
  width: 8px;
}
.container-legendas::-webkit-scrollbar-button { 
  display: none;
}
.container-legendas::-webkit-scrollbar-track {
  border-radius: 10px;
}
.container-legendas::-webkit-scrollbar-thumb { 
  border-radius: 4px;
  background: #cacaca;
}
@media(max-width: 1020px) {
  .container-legendas {
    margin-left: 0;
    margin-top: 20px;
    width: auto;
    min-height: 200px;
  }
}
.container-legendas .title {
  /* background: #e9e9e9; */
  /* position: absolute; */
  padding-bottom: 8px;
  font-size: 1rem;
  width: fit-content;
}
.container-legendas .normalize-height {
  position: relative;
  /* overflow: hidden; */
  padding-top: 56.25%;
  width: 100%;
  height: 0;
}
.actions-transmition {
  padding: 16px 15px 16px 60px;
}
.container-vid2 {
  background-color: #e9e9e9;
  margin-left: 20px;
  border-radius: 10px;
}

.myCanvas{
  display: flex;
}


/* andr */
/* ! no alterar as propriedades */
.disabled {
  color: #adadad;
}
.container-gridview {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 32px 15px 16px 60px;
  user-select: none;
}
@media(max-width: 1020px) {
  .container-gridview {
    flex-direction: column;
  }
}
.container-vid {
  background-color: #e9e9e9;
  border-radius: 5px; /* conforme prototipo*/
  display: flex;
  align-items: center;
  justify-content: center;
  font: normal normal 400 16px/25px Poppins;
  letter-spacing: 0px;
  width: 100%;
  height: auto;
  color: black;
}
.container-vid > div {
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  padding-bottom: 56.25%; /* 16:9 => resoluo full hd */
  width: 100%;
  height: 0;
}
.container-vid .msg {
  padding: 16px;
}
video, iframe {
  border-radius: 5px !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
video::-webkit-media-controls {
  visibility: hidden;
}

video::-webkit-media-controls-enclosure {
  visibility: visible;
}
