.Form-addUser {
  display: flex;
  height: 600px;
}
@media (max-width: 768px) {
  .Form-addUser .row > div {
    padding: 0 !important;
  }
  .Form-addUser {
    padding: 0 !important;
  }
  .Form-addUser .grouping {
    /* width: 10rem !important; */
    width: 100% !important;
  }
  .Form-addUser > input {
    /* width: 10rem !important; */
    width: 100% !important;
  }
}

.title-contrato {
  text-align: center;
  font: normal normal 600 20px/30px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

.inputs-addUser {
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  text-align: left;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  color: #121212;
  border-style: solid;
  border-color: #e9e9e9;
  border-width: 2px;
  border-radius: 10px;
  z-index: 999;
  height: 55px;
}
@media (max-width: 768px) {
  .inputs-addUser {
    width: 100% !important;
  }
}
