@import url(./assets/fonts.css);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.fade-in {
  animation: fadeIn;
  animation-duration: 0.6s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

body {
  font-family: 'Poppins', sans-serif !important;
}

html {
  scroll-behavior: smooth;
}

/* atualizao do estilo do scroll */
*::-webkit-scrollbar {
  width: 8px;
}
*::-webkit-scrollbar-button { 
  display: none;
}
*::-webkit-scrollbar-track {
  border-radius: 10px;
}
*::-webkit-scrollbar-thumb { 
  border-radius: 4px;
  background: #cacaca;
}


/* prevents the mess of bootstrap */
.col {
  padding: 0 !important;
  margin: 0 !important;
}
.row {
  padding: 0 !important;
  margin: 0 !important;
}

/* adjustments in style */
.app_container {
  padding: 16px 32px;
  min-height: 150vh;
}

.page {
  padding: 0 !important;
  margin: 0 !important;
}

.bell {
  display: flex !important;
  justify-content: flex-end !important;
  padding-right: 56px !important;
}

.row-nav {
  display: flex !important;
  justify-content: flex-end !important;
  flex-wrap: nowrap !important;
}

.row-nav:last-child {
  padding: 0 !important;
}

.title-row {
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  padding-top: 48px !important;
}

.title-col {
  padding-left: 60px !important;
  width: 100% !important;
}

.main_container {
  padding: 32px 16px 32px 60px !important;
  min-height: 600px !important;
}

.button-col {
  padding: 0 !important;
  max-width: 100% !important;
  display: flex;
  /* justify-content: space-between; */
}

.pickerContainer > div {
  width: 100% !important;
}

.mobile-menu {
  display: none;
}

.header-modal {
  padding: 20px !important;
  align-items: center !important;
  font: 18px !important;
  font-weight: 600 !important;
  margin: 0 !important;
}
.closebox-modal {
  width: 100% !important;
  display: flex !important;
  justify-content: flex-end !important;
  padding: 8px 16px !important;
}
.closebox-modal > svg {
  cursor: pointer !important;
  font-size: 20px !important;
}

@media (max-width: 768px) {
  .title-col {
    padding-left: 0 !important;
  }
  .button-col {
    flex-direction: column;
  }
  .button-col > button {
    margin-right: 0 !important;
    width: 100%;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
    padding: 8px 32px !important;
  }
  .main_container {
    padding: 16px 0 !important;
  }
  .grid-charts {
    padding: 0 !important;
  }
  .container-gridview,
  .actions-transmition {
    padding: 16px 0 !important;
  }
  .row-nav {
    display: none !important;
  }
}