:root {
  --primary: #fff;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background: #fcfcfc 0% 0% no-repeat padding-box;
  border-radius: 10px;
  opacity: 1;
  text-align: left;
  font: normal normal 600 16px/25px Poppins;
}

.btn--outline {
  background: #feff46 0% 0% no-repeat padding-box;
  width: 156px;
  height: 54px;
  border-radius: 10px;
  opacity: 1;
  color: #121212;
  font-weight: 600;
  padding: 8px 20px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--footer {
  background: #189d0e 0% 0% no-repeat padding-box;
  width: 270px;
  height: 37px;
  border-radius: 6px;
  opacity: 1;
  text-align: left;
  font: normal normal normal 14px Poppins;
  letter-spacing: 0px;
  color: #ffffff;
  opacity: 1;
}

.btn--user {
  width: 144px;
  background-color: #feff46;
  color: #121212;
  font: normal normal 600 14px/21px Poppins;
  letter-spacing: 0px;
  border-radius: 10px;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 16px;
}

.btn--large {
  width: 367px;
  height: 54px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  transition: 250ms;
  background: #242424;
  color: #ffffff;
  transition: 250ms;
}

.btn--footer:hover {
  transition: all 0.3s ease-out;
  color: #ffffff;
  background-color: darkgreen;
  transition: 250ms;
}

@media only screen and (max-width: 575px) {
  .btn--large {
    width: 200px;
    height: 100px;
    text-align: center;
  }
}
