.files-title {
  text-align: left;
  font: normal normal 600 20px/30px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

.container-files {
  display: flex;
  justify-content: flex-start;
  flex-flow: row wrap;
  padding: 15px 15px 0 60px;
  min-height: 600px;
}

.img-card {
  height: 400px;
  width: 240px;
  margin: 15px;
  padding: 12px;
  padding-bottom: 20px;
  border-radius: 10px;
}

.img-title-row {
  margin-bottom: 5px;
  padding-left: 8px;
  padding-right: 15px;
}

.img-title-col {
  /* display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between; */
}

.trash-col {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.trashbutton {
  border: none;
  background-color: transparent;
}

.Block-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  width: 60px;
  height: 60px;
  border-radius: 10px;
  margin: 5px;
  overflow: hidden;
  border: hidden;
}

.Block-container-sel {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  width: 60px;
  height: 60px;
  border-radius: 10px;
  margin: 5px;
  overflow: hidden;
  border-color: #1664c5;
}

.Block-button {
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #e9e9e9;
  width: 60px;
  height: 60px;
  border-radius: 10px;
  margin: 5px;
}

.container-blocks {
  display: flex;
  flex-flow: row wrap;
}
