.login-container {
  display: grid;
  grid-template-columns: 6fr 6fr;
  grid-template-rows: 85px 100vh;
  grid-template-areas:
    "nav nav"
    "a1 a2";
}

.navbar-login {
  display: flex;
  grid-area: nav;
  background: #ffffff 0% 0% no-repeat padding-box;
  top: 0px;
  left: 0px;
  height: 85px;
  /* width: 100%; */
  font: normal normal 600 16px/25px "Poppins";
  letter-spacing: 0px;
  justify-content: center;
  align-items: center;
  position: static;
  z-index: 999;
}

.container-left {
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  display: flex;
  flex: 1;
  grid-area: a1;
  min-height: 500px;
}

.container-right {
  display: flex;
  flex: 1;
  grid-area: a2;
  min-height: 450px;
  padding-top: 15vh;
  align-items: flex-start;
}

.mockup-login {
  justify-self: center;
  align-self: flex-start;
  width: 541px;
  height: 330px;
}

.box {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  height: 600px;
  max-width: 370px;
}
.form-login {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  height: 500px;
  max-width: 370px;
}

.title-login {
  text-align: center;
  font: normal normal 800 25px/35px Poppins;
  letter-spacing: 0.25px;
  color: #121212;
  opacity: 1;
}

.login-labels {
  text-align: left;
  font: normal normal bold 12px/18px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

input {
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  width: 344px;
  height: 54px;
  padding-left: 20px;
  padding-right: 20px;
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 2px solid #e9e9e9;
  border-radius: 10px;
  opacity: 1;
  text-align: left;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  /* color: #cccccc; */
}
input::placeholder {
  color: #cccccc;
}

.login-button {
  width: 344px;
  height: 54px;
  text-align: center;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  color: #121212;
  background: #e9e9e9 0% 0% no-repeat padding-box;
  border-radius: 10px;
  border: none;
}

.login-button:hover {
  background: transparent linear-gradient(270deg, #48ab16 0%, #378a13 100%) 0%
    0% no-repeat padding-box;
  color: #ffffff;
  transition: 300ms;
  box-shadow: 0px 2px 10px #48ab16;
  cursor: pointer;
}

::placeholder {
  color: #e9e9e9;
}

.forgot-pass {
  text-align: center;
  font: normal normal 600 16px/25px Poppins;
  letter-spacing: 0px;
  color: #0d396f;
  opacity: 1;
}

@media only screen and (max-width: 720px) {
  .login-container {
    background-color: #ffffff;
    display: grid;
    grid-template-columns: 10fr;
    grid-template-rows: 1fr 4fr;
    grid-template-areas:
      "nav"
      "a1";
  }

  .container-right {
    display: none;
  }
}
