.ct-ld-page {
  width: 100%;
  box-sizing: border-box;
  padding: 0px;
}
.nl-tl {
  height: 100vh;
  padding-top: 50px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.nl-tr {
  height: 100vh;
  padding: 0;
}
.nl-ml {
  height: 100vh;
  background-color: #1664c5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 25px;
  padding-top: 200px;
}
.nl-mr {
  height: 100vh;
  background-color: #1664c5;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 130px;
  padding-right: 0px;
}
.nl-bt {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 80px;
  padding-right: 80px;
  padding-bottom: 100px;
  padding-top: 80px;
}
.nl-ft1 {
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}
.nl-ft2 {
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}
.nl-ft3 {
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.nl-ft4 {
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.box1 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: left;
  min-height: 500px;
  width: 450px;
  padding: 20px;
}

.bar-effect {
  align-self: flex-start;
  width: 129px;
  height: 15px;
  background: #feff46 0% 0% no-repeat padding-box;
  border-radius: 10px;
  margin-bottom: 10px;
  opacity: 1;
}

.title-1 {
  text-align: left;
  letter-spacing: 0.25px;
  color: #121212;
  opacity: 1;
  font: normal normal 800 25px/35px Poppins;
  align-self: flex-start;
}

.title-2 {
  letter-spacing: 0.17px;
  color: #121212;
  opacity: 1;
  font: normal normal normal 17px/35px Poppins;
  text-align: left;
  align-self: flex-start;
}

.fa-arrow-right {
  margin-left: 8px;
}

.box2 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  text-align: left;
  min-height: 500px;
  width: 450px;
  height: 45vh;
}

.title-3 {
  text-align: left;
  font: normal normal 800 25px/35px Poppins;
  letter-spacing: 0.25px;
  color: #0d396f;
  opacity: 1;
}
.title-4 {
  text-align: left;
  font: normal normal normal 17px/35px Poppins;
  letter-spacing: 0.17px;
  color: #ffffff;
  opacity: 1;
}
.title-5 {
  text-align: left;
  font: normal normal normal 17px/35px Poppins;
  letter-spacing: 0.17px;
  color: #ffffff;
  opacity: 1;
}
.icon-1 {
  top: 1273px;
  left: 0px;
  width: 215px;
  height: 238px;
  align-self: flex-start;
}
.icon-2 {
  width: 143px;
  height: 264px;
  align-self: flex-end;
}
.mockup {
  width: 541px;
  height: 330px;
  margin-top: 50px;
}

.title-6-container {
  max-width: 1100px;
  min-height: 72px;
  opacity: 1;
  margin-bottom: 53px;
  padding-left: 20px;
  padding-right: 20px;
}

.title-6 {
  text-align: center;
  font: normal normal 800 25px/35px Poppins;
  letter-spacing: 0.25px;
  color: #121212;
}

.grouping {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.labels {
  text-align: left;
  font: normal normal bold 12px/18px Poppins;
  letter-spacing: 0px;
  color: #ffffff;
}
.text-input {
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  text-align: left;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  color: #ffffff;
  background-color: transparent;
  border-style: solid;
  border-color: white;
  border-width: 2px;
  border-radius: 10px;
  z-index: 400;
  height: 55px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 20px;
  padding-right: 20px;
}

::placeholder {
  color: white;
}

.submit:hover {
  background-color: #242424;
  color: #ffffff;
  cursor: pointer;
  transition: 250ms;
}

textarea {
  background-color: transparent;
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  color: #ffffff;
  text-align: left;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  background-color: transparent;
  border-style: solid;
  border-color: white;
  border-width: 2px;
  border-radius: 10px;
  z-index: 400;
  resize: none;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 20px;
  padding-right: 20px;
  height: 155px;
}
.submit {
  background: #224f0e 0% 0% no-repeat padding-box;
  box-shadow: 0px 15px 30px #224f0e33;
  border: 0px;
  height: 55px;
  border-radius: 10px;
  opacity: 1;
  text-align: center;
  font: normal normal 600 16px/25px Poppins;
  color: #ffffff;
  margin-top: 25px;
  width: 344px;
}

.bt-form {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: #4a952a;
  border-radius: 10px;
  padding-top: 50px;
  padding-bottom: 50px;
  position: relative;
}

.form-card-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px;
}

.form-card-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px;
}

.footer-title1 {
  text-align: left;
  font: normal normal bold 16px/25px Poppins;
  letter-spacing: 0px;
  color: #ffffff;
  opacity: 1;
  margin-bottom: 10px;
}

.footer-title2 {
  text-align: left;
  font: normal normal normal 14px/21px Poppins;
  letter-spacing: 0px;
  color: #ffffff;
  opacity: 1;
  margin-bottom: 5px;
  width: 250px;
}

.social-buttons {
  display: flex;
  flex: 1;
  justify-content: space-between;
  letter-spacing: 0px;
  color: #ffffff;
  opacity: 1;
  width: 160px;
  padding-left: 10px;
  padding-right: 10px;
}

.fa-facebook {
  color: #1664c5;
  background: radial-gradient(white 0%, white 50%, transparent 75%);
}

.fa-instagram {
  color: white;
  border-radius: 5px;
  background: #f09433;
  background: -moz-linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  background: -webkit-linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 );
}

.fa-youtube {
  color: #cd201f;
  background: radial-gradient(
    white 0%,
    white 10%,
    transparent 50%,
    transparent 100%
  );
}

.fa-whatsapp {
  align-self: start;
  margin-right: 8px;
}

@media only screen and (max-width: 1199px) {
  .nl-tr {
    display: none;
  }
}

@media only screen and (max-width: 575px) {
  .nl-tr {
    display: none;
  }

  .nl-bt {
    padding-left: 0px;
    padding-right: 0px;
  }

  .mockup {
    width: 294px;
    height: 153px;
    margin-top: 50px;
  }

  .icon-1 {
    top: 1273px;
    left: 0px;
    width: 120px;
    height: 132px;
    align-self: flex-start;
  }
  .icon-2 {
    width: 79px;
    height: 147px;
    align-self: flex-end;
  }
  .box1 {
    min-height: 330px;
    width: 335px;
    margin-left: 10px;
  }
  .box2 {
    min-height: 330px;
    width: 335px;
    margin-left: 10px;
  }
  .title-6-container {
    max-width: 360px;
    min-height: 72px;
    opacity: 1;
    margin-bottom: 53px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .form-row {
    width: 300px;
    padding: 0px;
  }
  .bt-form {
    width: 100%;
  }
  .form-card-left {
    align-self: stretch;
    margin: 0px;
  }

  .form-card-right {
    align-self: stretch;
    margin: 0px;
  }
  .text-input {
    width: 300px;
  }
  textarea {
    width: 300px;
    margin: 0px;
  }
  .submit {
    width: 300px;
  }
  .nl-ft1 {
    padding-top: 30px;
  }
  .nl-ft3 {
    padding-bottom: 50px;
  }
}

.alert-contact {
  position: absolute;
  top: 20px;
  z-index: 900;
}