.list-group-container {
  padding-top: 20px;
  padding-bottom: 20px;
}
.list-header-container {
  /* padding-right: 25px; */
  /* padding-left: 25px; */
  /* padding-top: 20px; */
  /* padding-bottom: 20px; */
  padding: 8px;
  border: thin;
  background-color: transparent;
}
.list-item-container {
  /* padding-right: 25px; */
  /* padding-left: 25px; */
  padding: 8px;
  border: thin;
  background-color: transparent;
}

.header-cols {
  text-align: left;
  align-items: center;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  color: #8f8f8f;
  opacity: 1;
  padding-right: 0px;
  padding-left: 0px;
}

.item-cols {
  display: flex;
  text-align: left;
  font: normal normal normal 16px/25px Poppins;
  letter-spacing: 0px;
  color: #8f8f8f;
  opacity: 1;
  padding-right: 0px;
  padding-left: 0px;
  align-items: center;
  justify-content: center;
  height: 60px;
}

.avatar-div {
  display: flex;
  flex: 1;
  flex-direction: row;
  text-align: left;
  justify-content: flex-start;
  padding-left: 30px;
}
.username-div {
  display: flex;
  flex: 1;
  flex-direction: row;
  text-align: left;
  justify-content: flex-start;
}

.avatar {
  height: 34px;
  width: 34px;
  margin-right: 20px;
}
.title-users {
  text-align: center;
  font: normal normal bold 12px/18px Poppins;
  letter-spacing: 0px;
  color: #838383;
  opacity: 1;
}
.switch-button,
.switch-button:hover,
.switch-button:focus,
.switch-button:active {
  display: flex;
  flex-direction: row;
  text-align: center;
  font: normal normal normal 14px/21px Poppins;
  letter-spacing: 0px;
  color: #8f8f8f;
  opacity: 1;
  background-color: transparent;
  width: 224px;
  outline: none !important;
  box-shadow: none !important;
  justify-content: center;
}
.switch-actual {
  margin-left: 10px;
}
.item-Rows {
  box-shadow: 0px 3px 10px #cccccc4d;
  border-radius: 5px;
  background-color: transparent;
  margin-bottom: 0px;
}

.fa-calendar-alt {
  padding-right: 5px;
}

.menu,
menu:hover,
menu:focus,
menu:active {
  background-color: white;
  outline: none !important;
  box-shadow: none !important;
}

.fa-ellipsis-v {
  color: black;
}

.fa-trash {
  margin-right: 10px;
}

.fa-edit {
  margin-right: 10px;
}

.fakeicon {
  margin-right: 10px;
}

/* pagination */
.pagination {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px;
  font-size: 14px;
}
.pagination button {
  outline: none;
  border: none;
  padding: 8px 32px;
  background: #cacaca60;
  border-radius: 5px;
}
.pagination button:last-child {
  margin-left: 16px;
}