.navbar {
  display: flex;
  background: #ffffff 0% 0% no-repeat padding-box;
  top: 0px;
  left: 0px;
  height: 85px;
  /* width: 100vw; */
  font: normal normal 600 16px/25px "Poppins";
  letter-spacing: 0px;
  justify-content: center;
  align-items: center;
  position: sticky;
  box-shadow: 0px 3px 10px #cccccc4d;
  z-index: 999;
}
.navbar ul {
  margin-bottom: 0;
}

img {
  width: 110px;
  height: 50px;
}

.navbar-logo {
  position: absolute;
  justify-self: flex-start;
  cursor: pointer;
  align-items: center;
  left: 65px;
  width: 110px;
  height: 50px;
}

.fa-play {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: center;
  margin-right: 2rem;
}

.nav-item {
  /* height: 80px; */
  margin-top: 0;
  margin-bottom: 0 !important;
}

.nav-links {
  color: #121212;
  margin-right: 30px;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  text-decoration: none !important;
}

.nav-links:hover {
  /* border-bottom: 4px solid #fff; */
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #121212;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #ffffff;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #121212;
    color: #ffffff;
    border-radius: 0;
  }

  .navbar-logo {
    justify-self: start;
    cursor: pointer;
    align-items: center;
    top: 18px;
    left: 65px;
    width: 110px;
    height: 50px;
    position: absolute;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #121212;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #242424;
    padding: 14px 20px;
    border: 1px solid #242424;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #242424;
    color: #ffffff;
    transition: 250ms;
  }
}
