.cameras-title {
  text-align: left;
  font: normal normal 600 20px/30px Poppins;
  letter-spacing: 0px;
  color: #121212;
  opacity: 1;
}

.cam-btn {
  color: white;
  margin-left: 15px;
}

.container-cam {
  /* 
  display: flex;
  justify-content: flex-start;
  flex-flow: row wrap; 
  */
  display: grid;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(230px, 2fr));
  /*  */
  /* padding-left: 45px; */
  /* padding-top: 15px; */
  padding: 15px 15px 0 60px;
  min-height: 600px;
  /* overflow: auto; */
}

.card-cam {
  background-color: #1664c5;
  border-radius: 10px;
  opacity: 1;
  /* width: 230px; */
  /* height: 206px; */
  max-height: 310px;
  /* margin: 15px; */
  padding: 12px;
  padding-bottom: 20px;
}
@media (max-width: 768px) {
  .card-cam {
    max-height: max-content !important;
  }
}

.pen-col {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.cardContainer{
  display: flex;
  flex-wrap: wrap;
}

.cam-title-row {
  padding-top: 70px;
  margin-bottom: 20px;
}
.card-title-row {
  margin-bottom: 5px;
}
